import React, { useEffect, useState } from "react";
import {
  Button,
  Layout,
  Modal,
  Input,
  InputNumber,
  Form,
  Dropdown,
  Menu,
} from "antd";
import {
  DownOutlined,
  StockOutlined,
  LoginOutlined,
  LogoutOutlined,
  CloudUploadOutlined,
  CloudDownloadOutlined,
} from "@ant-design/icons";
import "./App.css";
import DragSortingTable from "./components/DragSortingTable";
import { getColumns } from "./ColumnsConfig";
import {
  checkVersion,
  formatMoney,
  formatNumber,
  getCurrentDateTime,
} from "./utils";
import { FILE_NOT_FOUND, FILE_NAME } from "./constant";
import { startLoading, stopLoading } from "./Loader";
const { Header, Content } = Layout;
const _ = require("lodash");

const layoutForm = {
  labelCol: { span: 6 },
  wrapperCol: { span: 18 },
};

const SCOPE = "https://www.googleapis.com/auth/drive.file";
const discoveryUrl =
  "https://www.googleapis.com/discovery/v1/apis/drive/v3/rest";
let gAuth = "";

const runCheckVersion = () => {
  setInterval(function () {
    if (!localStorage.getItem("checking_version")) {
      checkVersion();
    }
  }, 60000);
};

const App = () => {
  const [isLogined, setIsLogined] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [isUpdate, setIsUpdate] = useState(false);
  const [data, setData] = useState([]);
  const [currentFormData, setCurrentFormData] = useState({});

  const onAddRow = () => {
    setCurrentFormData({});
    setIsUpdate(false);
    setShowModal(true);
  };

  const onEditRow = (record) => {
    setCurrentFormData(record);
    setIsUpdate(true);
    setShowModal(true);
  };

  const onDeleteRow = (record) => {
    const newData = data.filter((item) => item.code !== record.code);
    setData(newData);
    localStorage.setItem("data", JSON.stringify(newData));
  };

  const handleCancelModal = () => {
    setShowModal(false);
  };

  const handleSaveModal = () => {
    if (isUpdate) {
      const newData = data.map((item) => {
        if (item.code === currentFormData.code.toUpperCase()) {
          item.cut_loss = currentFormData.cut_loss;
          item.buyFrom = currentFormData.buyFrom;
          item.buyTo = currentFormData.buyTo;
          item.target = currentFormData.target;
          item.buy_number = currentFormData.buy_number;
          item.updated_at = getCurrentDateTime();
        }
        return item;
      });
      localStorage.setItem("data", JSON.stringify(newData));
      setData(newData);
    } else {
      currentFormData.code = currentFormData.code.toUpperCase();
      currentFormData.updated_at = getCurrentDateTime();
      const newData = [...data, currentFormData];
      localStorage.setItem("data", JSON.stringify(newData));
      setData(newData);
    }
    setShowModal(false);
  };

  const initClient = () => {
    try {
      window.gapi.client
        .init({
          apiKey: process.env.REACT_APP_GOOGLE_DRIVE_API_KEY,
          clientId: process.env.REACT_APP_GOOGLE_DRIVE_CLIENT_ID,
          scope: SCOPE,
          discoveryDocs: [discoveryUrl],
        })
        .then(() => {
          gAuth = window.gapi.auth2.getAuthInstance();
          gAuth.isSignedIn.listen(updateSigninStatus);
          updateSigninStatus();
        });
    } catch (e) {
      console.log(e);
    }
  };

  const signInFunction = () => {
    gAuth.signIn();
  };

  const signOutFunction = () => {
    gAuth.signOut();
  };

  const updateSigninStatus = () => {
    const user = gAuth.currentUser.get();
    if (user.xc == null) {
      console.log("User logout!");
      setIsLogined(false);
    } else {
      const isAuthorized = user.hasGrantedScopes(SCOPE);
      console.log("User login!");
      if (isAuthorized) {
        console.log("User is authorized!");
        setIsLogined(true);
        //we will put the code of the third step here
      }
    }
  };

  const readFileData = (fileId) => {
    return new Promise((resolve, reject) => {
      var request = window.gapi.client.drive.files.get({
        fileId: fileId,
        alt: "media",
      });
      request.then(
        function (response) {
          resolve(response.body); //response.body contains the string value of the file
        },
        function (error) {
          reject(error);
        }
      );
    });
  };

  const getFileId = () => {
    return new Promise((resolve, reject) => {
      window.gapi.client.drive.files
        .list({
          pageSize: 10,
          fields: "*",
          q: `name='${FILE_NAME}'`,
        })
        .then(
          function (response) {
            const file = response?.result?.files[0];
            if (file) resolve(file.id);
            resolve(FILE_NOT_FOUND);
          },
          function (error) {
            reject(error);
          }
        );
    });
  };

  const createNewFile = () => {
    const boundary = "foo_bar_baz";
    const delimiter = "\r\n--" + boundary + "\r\n";
    const close_delim = "\r\n--" + boundary + "--";
    var fileData = localStorage.getItem("data");
    var contentType = "text/plain";
    var metadata = {
      name: FILE_NAME,
      mimeType: contentType,
    };

    var multipartRequestBody =
      delimiter +
      "Content-Type: application/json; charset=UTF-8\r\n\r\n" +
      JSON.stringify(metadata) +
      delimiter +
      "Content-Type: " +
      contentType +
      "\r\n\r\n" +
      fileData +
      "\r\n" +
      close_delim;

    var request = window.gapi.client.request({
      path: "https://www.googleapis.com/upload/drive/v3/files",
      method: "POST",
      params: { uploadType: "multipart" },
      headers: {
        "Content-Type": "multipart/related; boundary=" + boundary + "",
      },
      body: multipartRequestBody,
    });
    request.execute(function (file) {
      stopLoading();
    });
  };

  const deleteFile = (fileId) => {
    return new Promise((resolve, reject) => {
      var request = window.gapi.client.drive.files.delete({
        fileId: fileId,
      });
      request.then(
        function (response) {
          resolve(response.body === "");
        },
        function (error) {
          reject(false);
        }
      );
    });
  };

  const syncData = async (type) => {
    startLoading();
    const fileId = await getFileId();
    if (fileId !== FILE_NOT_FOUND) {
      const fileData = await readFileData(fileId);
      const localData = localStorage.getItem("data");
      let newData = [];
      if (type === "upload") {
        newData = _.unionBy(
          JSON.parse(localData),
          JSON.parse(fileData),
          "code"
        );
      }
      if (type === "download") {
        newData = _.unionBy(
          JSON.parse(fileData),
          JSON.parse(localData),
          "code"
        );
      }
      setData(newData);
      localStorage.setItem("data", JSON.stringify(newData));
      if (await deleteFile(fileId)) {
        createNewFile();
      }
    } else {
      // upload localstorage to drive
      createNewFile();
    }
  };

  useEffect(() => {
    const dataStorage = localStorage.getItem("data")
      ? JSON.parse(localStorage.getItem("data"))
      : [];
    setData(dataStorage);
    if (process.env.NODE_ENV === "production") {
      setTimeout(() => {
        checkVersion();
        runCheckVersion();
      }, 5000);
    }
  }, []);

  useEffect(() => {
    // import google api
    const script = document.createElement("script");
    script.onload = () => {
      window.gapi.load("client:auth2", initClient);
    };
    script.src = "https://apis.google.com/js/api.js";
    document.body.appendChild(script);
  });

  const menu = (
    <Menu>
      <Menu.Item onClick={onAddRow} key="1" icon={<StockOutlined />}>
        Thêm mới
      </Menu.Item>
      {isLogined && (
        <Menu.Item
          onClick={() => syncData("upload")}
          key="2"
          icon={<CloudUploadOutlined />}
        >
          Upload
        </Menu.Item>
      )}
      {isLogined && (
        <Menu.Item
          onClick={() => syncData("download")}
          key="3"
          icon={<CloudDownloadOutlined />}
        >
          Download
        </Menu.Item>
      )}
      {!isLogined && (
        <Menu.Item onClick={signInFunction} key="4" icon={<LoginOutlined />}>
          Đăng nhập
        </Menu.Item>
      )}
      {isLogined && (
        <Menu.Item onClick={signOutFunction} key="5" icon={<LogoutOutlined />}>
          Đăng xuất
        </Menu.Item>
      )}
    </Menu>
  );

  return (
    <>
      <Layout className="App">
        <Header>
          <div>
            <p className="title">
              Stocks Watchlist {`(v${process.env.REACT_APP_VERSION})`}
            </p>
            <p className="sub-title">Công cụ quản lý danh mục đầu tư</p>
          </div>
          <div>
            <Dropdown overlay={menu} trigger={["click"]}>
              <Button type="primary">
                Menu <DownOutlined />
              </Button>
            </Dropdown>
          </div>
        </Header>
        <Content>
          <DragSortingTable
            dataSource={data}
            columns={getColumns(onEditRow, onDeleteRow)}
          />
        </Content>
      </Layout>

      <Modal
        visible={showModal}
        title={isUpdate ? "Chỉnh sửa mã" : "Thêm mã mới"}
        onCancel={handleCancelModal}
        footer={[
          <Button key="back" onClick={handleCancelModal}>
            Hủy bỏ
          </Button>,
          <Button key="submit" type="primary" onClick={handleSaveModal}>
            Lưu
          </Button>,
        ]}
      >
        <Form {...layoutForm} name="basic" layout="horizontal">
          <Form.Item label="Mã CK">
            <Input
              disabled={isUpdate}
              value={currentFormData.code}
              onChange={(e) =>
                setCurrentFormData({ ...currentFormData, code: e.target.value })
              }
            />
          </Form.Item>
          <Form.Item label="Cut loss">
            <InputNumber
              min={0}
              step={0.1}
              style={{ width: "100%" }}
              value={currentFormData.cut_loss}
              onChange={(value) =>
                setCurrentFormData({
                  ...currentFormData,
                  cut_loss: value,
                })
              }
            />
          </Form.Item>
          <Form.Item label="Khoảng mua">
            <InputNumber
              min={0}
              step={0.1}
              value={currentFormData.buyFrom}
              onChange={(value) =>
                setCurrentFormData({
                  ...currentFormData,
                  buyFrom: value,
                })
              }
            />
            <span style={{ marginLeft: 10, marginRight: 10 }}>-</span>
            <InputNumber
              min={0}
              step={0.1}
              value={currentFormData.buyTo}
              onChange={(value) =>
                setCurrentFormData({
                  ...currentFormData,
                  buyTo: value,
                })
              }
            />
          </Form.Item>
          <Form.Item label="Số lượng">
            <InputNumber
              min={0}
              style={{ width: "100%" }}
              value={
                currentFormData.buy_number &&
                formatNumber(currentFormData.buy_number)
              }
              onChange={(value) =>
                setCurrentFormData({
                  ...currentFormData,
                  buy_number: value,
                })
              }
            />
          </Form.Item>
          <Form.Item label="Target">
            <InputNumber
              min={0}
              step={0.1}
              style={{ width: "100%" }}
              value={currentFormData.target}
              onChange={(value) =>
                setCurrentFormData({
                  ...currentFormData,
                  target: value,
                })
              }
            />
          </Form.Item>
          <Form.Item label="Lợi nhuận">
            <Input.Group>
              <Input
                disabled
                placeholder="%"
                style={{ width: "25%" }}
                value={
                  currentFormData.target && currentFormData.buyTo
                    ? (
                        ((currentFormData.target - currentFormData.buyTo) *
                          100) /
                        currentFormData.buyTo
                      ).toFixed(2) + "%"
                    : ""
                }
              />

              <Input
                placeholder="Ước tính theo giá mua lớn nhất"
                style={{ width: "75%" }}
                value={
                  currentFormData.target &&
                  currentFormData.buyTo &&
                  currentFormData.buy_number &&
                  formatMoney(
                    currentFormData.buyTo *
                      currentFormData.buy_number *
                      (((currentFormData.target - currentFormData.buyTo) *
                        100) /
                        currentFormData.buyTo) *
                      10,
                    0
                  )
                }
              />
            </Input.Group>
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
};

export default App;
